stock.png"),"alt":"Stock produit chimique"}}),_c('p',[_c('em',[_vm._v("Figure 9 : Email du stock des produits chimique avec Excel")])])]),_c('br'),_c('p',[_vm._v("Comme le montre la figure 9, un email est envoyé automatiquement chaque mardi, contenant les données de stock sous forme de fichier Excel en pièce jointe. Pour cela, j’ai utilisé une requête SQL ciblant les produits concernés, puis Talend se chargeait de générer le fichier Excel et de l’envoyer par email aux destinataires concernés. Ce processus a permis de fiabiliser et d’automatiser la diffusion d’informations tout en respectant les formats attendus.")]),_c('br'),_vm._v("* "),_c('h3',[_vm._v("1.4. Manipuler des données hétérogènes")]),_c('p',[_vm._v("Ici, tu peux mettre en valeur le fait que Talend manipule des données provenant de sources variées (fichiers plats, bases de données, API, etc.). J'ai travaillé sur des flux manipulant des données issues de sources hétérogènes telles que des bases de données relationnelles (PostgreSQL, Oracle), des fichiers CSV, et des exports Excel. J'ai utilisé Talend pour standardiser, filtrer et transformer ces données, en assurant leur compatibilité avec les systèmes cibles. (csv + bases de données sql server + DBeaver)")]),_c('div',[_c('img',{staticClass:"img-flux",attrs:{"src":require("@/assets/organisation-flux.png"),"alt":"Flux"}}),_c('p',[_c('em',[_vm._v("Figure 10 : Organisation des flux de données")])])]),_c('div',[_c('img',{staticClass:"img-db",attrs:{"src":require("@/assets/DBeaver.png"),"alt":"DBeaver"}}),_c('p',[_c('em',[_vm._v("Figure 11 : Interface du logiciel DBeaver")])])]),_c('h2',[_vm._v(" Compétence 5 : Conduire un projet")]),_c('h3',[_vm._v("AC 1 : Identifier les processus présents dans une organisation en vue d'améliorer les systèmes d'information")]),_c('p',[_vm._v("Explication : Il s'agit d'analyser les processus métiers et techniques existants pour détecter les points d'amélioration dans les systèmes d'information. Application à ton stage (Fiabili