umenté ce besoin et proposé une intégration avec un outil comme Slack ou PagerDuty.\" Livrable : Un tableau de besoins classés par priorité (ex : fiabilité, rapidité, traçabilité). (mail dwh)")]),_c('div',[_c('img',{attrs:{"src":require("@/assets/email-dwh.png"),"alt":"DWH"}}),_c('p',[_c('em',[_vm._v("Figure 16 : Email du chargement des données journalier")])])]),_c('h3',[_vm._v("AC 4 : Définir et mettre en œuvre une démarche de suivi de projet")]),_c('p',[_vm._v("Explication : Planifier, suivre et rendre compte de l'avancement. Application à ton stage : Outils de suivi : Créer un tableau Kanban (Trello, Jira) avec les tâches : \"Analyse des flux\", \"Tests de fiabilité\", \"Déploiement\". Utiliser Git pour versionner les jobs Talend modifiés. Indicateurs : \"J'ai réduit le taux d'échec des flux de 40% à 5% en 2 mois, avec des rapports hebdomadaires partagés via Power BI.\" Rétrospective : Organiser une réunion bilan pour présenter les améliorations et les axes d'évolution. (Réunion Tanguy après problème)")]),_c('div',[_c('img',{attrs:{"src":require("@/assets/F01_105.png"),"alt":"Job millieu"}}),_c('p',[_c('em',[_vm._v("Figure 17 : Visualisation du chevauchement des jobs")])])])])
